import React from 'react';
import { Box, Container, Grid, Typography, IconButton, List, ListItem, Link } from '@mui/material';
import { Facebook, Twitter, Instagram, LinkedIn, YouTube, Phone, Email, LocationOn, Copyright } from '@mui/icons-material';

function Footer() {
  return (
    <Box
      component="footer"
      sx={{
        backgroundColor: 'black',
        color: 'white',
        py: 4,
        mt: 'auto',
      }}
    >
      <Container maxWidth="lg">
        <Grid container spacing={4}>
          {/* Company Info Section */}
          <Grid size={{ xs: 12, md: 6 }}>
            <Typography variant="h5" fontWeight="bold" gutterBottom>
              Your Company
            </Typography>
            <Typography variant="body2" color="grey.300" paragraph>
              We are dedicated to providing the best service to our customers. 
              Our mission is to deliver excellence in every aspect of our business.
            </Typography>
            
            <Box display="flex" alignItems="center" mb={1}>
              <LocationOn sx={{ mr: 1, color: 'secondary.main' }} />
              <Typography variant="body2" color="grey.300">
                123 Street Name, City, Country
              </Typography>
            </Box>
            
            <Box display="flex" alignItems="center" mb={1}>
              <Phone sx={{ mr: 1, color: 'secondary.main' }} />
              <Typography variant="body2" color="grey.300">
                +1 234 567 8900
              </Typography>
            </Box>
            
            <Box display="flex" alignItems="center">
              <Email sx={{ mr: 1, color: 'secondary.main' }} />
              <Typography variant="body2" color="grey.300">
                info@yourcompany.com
              </Typography>
            </Box>
          </Grid>

          {/* Social Media Section */}
          <Grid size={{ xs: 12, md: 6 }}>
            <Typography variant="h6" fontWeight="bold" gutterBottom>
              Follow Us
            </Typography>
            <Box display="flex" gap={2} flexWrap="wrap">
              {[
                { icon: <Facebook />, label: 'Facebook', color: '#1877F2' },
                { icon: <Twitter />, label: 'Twitter', color: '#1DA1F2' },
                { icon: <Instagram />, label: 'Instagram', color: '#E4405F' },
                { icon: <LinkedIn />, label: 'LinkedIn', color: '#0A66C2' },
                { icon: <YouTube />, label: 'YouTube', color: '#FF0000' },
              ].map((social) => (
                <IconButton
                  key={social.label}
                  aria-label={social.label}
                  sx={{
                    backgroundColor: 'rgba(255, 255, 255, 0.1)',
                    color: 'white',
                    '&:hover': {
                      backgroundColor: social.color,
                      transform: 'translateY(-3px)',
                      transition: 'all 0.3s ease',
                    },
                  }}
                >
                  {social.icon}
                </IconButton>
              ))}
            </Box>
          </Grid>
        </Grid>

        {/* Copyright Section */}
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          color="grey.400"
          sx={{ mt: 4, pt: 3, borderTop: '1px solid rgba(255, 255, 255, 0.1)' }}
        >
          <Box display="flex" alignItems="center">
            <Copyright sx={{ fontSize: 14, mr: 0.5 }} />
            <Typography variant="body2">
              2024 Your Company. All rights reserved.
            </Typography>
          </Box>
        </Box>
      </Container>
    </Box>
  );
}

export default Footer;
